body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #add8e6;
}

.readonlycontainer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.readonly {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.main {
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: 'Aardvark Cafe';
  position: absolute;
  width: 100vw;
}

.content {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  width: 800px;
}

.main > * {
  margin-top: 20px;
}

.topbar {
  top: 12px;
  position: fixed;
  align-self: flex-start;
  display: flex;
  flex-direction: row;
  align-items: center;
  -webkit-filter: drop-shadow(5px 5px 5px #dd2bbc);
  filter: drop-shadow(5px 5px 5px #dd2bbc);
  z-index: -1;
}

.input {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.input > input {
  margin-left: 20px;
  width: 400px;
}

.logo {
  -webkit-animation: logo-spin infinite 10s linear;
          animation: logo-spin infinite 10s linear;
  height: 50px;
}

.title {
  margin-left: 20px;
}

.previewbar {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.previewbar > * {
  margin-left: 20px;
}

.preview {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

canvas {
  z-index: 1;
}

.gif {
  position: absolute;
}

textarea {
  width: 100%;
  height: 100px;
}

@font-face {
  font-family: 'Aardvark Cafe';
  src: url(/xchange/static/media/Aardvark_Cafe.9911a64a.ttf);
}

@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

